
.page-page {

    .left {
        position: absolute;
        left: 16%;
        width: 580px;

        @media screen and (max-width: 1200px) {
            left: 50%;
            transform: translateX(-50%);
          }
    }
    
    .right {
        position: absolute;
        right: 16%;
        width: 580px;
        text-align: right;

        @media screen and (max-width: 1200px) {
            left: 50%;
            transform: translateX(-50%);
          }
    }

    p {
        color: #f1ced9;
        margin: 0;
        font-family: 'Roboto';
    }
    
    h1 {
        color: #ff2164;
        margin: 0;
        font-family: 'Coolvetica';

    .topHalf {
        font-weight: 400;
        font-size: 64px;
        line-height: 72px;
        position: absolute;
        animation-name: topHalfAnimation;
        animation-delay: 3s;
        animation-duration: 1s;
        animation-fill-mode: forwards;
        transform: translateY(-71%);
    }

    .bottomHalf {
        font-size: 42px;
        line-height: 64px;
        font-weight: 800;
        position: absolute;
        animation-name: bottomHalfAnimation;
        animation-delay: 3s;
        animation-duration: 1s;
        animation-fill-mode: forwards;
        transform: translateY(21%);
    }

}

    .glow-on-hover {
        color: #ff2164;
        font-size: 13px;
        font-weight: 400;
        letter-spacing: 4px;
        font-family: sans-serif;
        text-decoration: none;
        padding: 10px 14px 10px 18px;
        border: 1px solid;
        margin-top: 25px;
        float: left;
        white-space: nowrap;
        outline: none;
        background: #111;
        cursor: pointer;
        position: relative;
        z-index: 0;
        border-radius: 10px;
    }
    
    .glow-on-hover:before {
        content: '';
        background: linear-gradient(45deg, #ff2164, #f1ced9, #ff2164, #130e10, #ff2164);
        position: absolute;
        top: -2px;
        left:-2px;
        background-size: 400%;
        z-index: -1;
        filter: blur(5px);
        width: calc(100% + 4px);
        height: calc(100% + 4px);
        animation: glowing 20s linear infinite;
        opacity: 0;
        transition: opacity .3s ease-in-out;
        border-radius: 10px;
    }
    
    .glow-on-hover:active {
        color: #000
    }
    
    .glow-on-hover:active:after {
        background: transparent;
    }
    
    .glow-on-hover:hover:before {
        opacity: 1;
    }
    
    .glow-on-hover:after {
        z-index: -1;
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background: #111;
        left: 0;
        top: 0;
        border-radius: 10px;
    }
    
    @keyframes glowing {
        0% { background-position: 0 0; }
        50% { background-position: 400% 0; }
        100% { background-position: 0 0; }
    }
}